<script>
  import axios from 'axios';

  export default {
    name: "article",
    data() {
      return {
        article: {},
        errors: []
      }
    },
    created() {
      var articleId = this.$route.params.id

      console.log("Id: "+articleId)

      axios.get('/api/articles/'+articleId)
        .then(response => {
          this.article = response.data
        })
        .catch(e => {
          this.errors.push(e)
        })
    }
  }
</script>



