<script>
  import axios from 'axios';

  export default {
    name: "articles",
    data() {
      return {
        articles: [],
        errors: []
      }
    },
    created() {
      axios.get(`/api/articles/`)
        .then(response => {
          this.articles = response.data
        })
        .catch(e => {
          this.errors.push(e)
        })
    }
  }
</script>

